.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.pokemonContainer {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 2em;
  padding: 2em;
  max-width: 1200px;
  margin: auto;
}

.pokemonBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
  cursor: pointer;
}

.pokemonBox:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.pokemonBox img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 1em;
  
}

.pokemonName {
  color: #040431;
  font-family: "Bona Nova SC", serif;
  font-size: 1.2em;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}

.pokeTittle{
  font-family: "Kalam", cursive;
  font-weight: 900;
  font-style: normal;
  font-size: 30px;
  color: #200361;
  display: flex;
justify-content: center;
}
.SearchBox{
  display: flex;
  justify-content: center;
}

/* Center the spinner */
.spinner {
  border: 6px solid #f3f3f3; /* Light gray background for the spinner */
  border-top: 6px solid #f00000; /* Red top to give it a Pokemon theme */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 100px auto; /* Centers the spinner horizontally */
}

/* Spinner animation */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.pokemonType{

  border-radius: 10px;
    background: #09f063;
}

.pokemonType p{

  padding: 5px;
    margin: 0px;
    font-size: 12px;
    text-transform: capitalize;
    font-weight: 600;
    color: white;
}

.userInput{

  background-color: #f9f9f9;
  border: 2px solid #ccc;
  padding: 4px 15px;
  font-size: 1rem;
  color: #333;
  border-radius: 20px;
  font-family: monospace;
  width: 20em;
  box-sizing: border-box;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;

}


/* Style for the 'Data Not Found' message */
.data-not-found {
  background-color: #f8d7da; /* Light red background to indicate no data */
  color: #721c24; /* Dark red text for contrast */
  padding: 20px; /* Adds space around the text */
  border-radius: 10px; /* Rounded corners for a smooth look */
  text-align: center; /* Center the text horizontally */
  font-size: 1.2rem; /* Slightly larger font for emphasis */
  font-weight: bold; /* Makes the text bold for visibility */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  margin: 20px 0; /* Adds space above and below the message */
}

/* Optional: Adding animation for better user interaction */
@keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.data-not-found {
  animation: fadeIn 0.5s ease-in-out;
}


.dataNotFound{
  display: flex;
  justify-content: center;
}